
import { computed, defineComponent, reactive, toRefs, watch } from "vue";
import { getPlayListCatListApi } from "@/api/playList";
import { AxiosResponseProps } from "@/utils/request";
export default defineComponent({
	props: {
		groupId: {
			type: Number,
			default: 0,
		},
		visible: {
			type: Boolean,
			default: false,
		},
		current: {
			type: String,
			default: "全部歌单",
		},
	},
	emits: ["update:groupId", "update:visible", "confirm"],
	setup(props, { emit }) {
		interface PlayListCatList extends AxiosResponseProps {
			sub: any[];
			categories: any[];
		}
		const state = reactive({
			id: computed({
				get: () => props.groupId,
				set: (newV) => emit("update:groupId", newV),
			}),
			panelVisible: computed({
				get: () => props.visible,
				set: (newV) => emit("update:visible", newV),
			}),
			groupList: [{ name: "全部歌单", id: 0, active: true }],
			sub: [],
			categories: {},
			treeData: computed(() => {
				if (state.categories) {
					return Object.entries(state.categories).map(([catIndex, catItem]) => ({
						name: catItem,
						category: catIndex,
						children: state.sub.filter((subItem) => subItem.category === catIndex),
					}));
				} else {
					return [];
				}
			}),
		});
		watch(
			() => state.treeData,
			(newV) => {
				console.log(newV);
			}
		);
		watch(
			() => state.id,
			(newV) => {
				let findItem = state.groupList.find((item) => item.id === newV);
				if (findItem) {
					state.groupList = state.groupList.map((item) => {
						if (item.id === findItem.id) {
							return { ...item, active: true };
						} else {
							return { ...item, active: false };
						}
					});
					emit("confirm", findItem);
				}
			}
		);
		const getGroupList = () => {
			// getGroupListApi().then((res: AxiosResponseProps) => {
			// 	if (res.code === 200) {
			// 		state.groupList = [{ name: "全部视频", id: 0, active: true }].concat(res.data);
			// 		// console.log(res);
			// 	}
			// });
			getPlayListCatListApi().then((res: Partial<PlayListCatList>) => {
				if (res.code === 200) {
					state.categoryList = res.categories;
					state.sub = res.sub;
					// console.log(res);
				}
			});
		};
		getGroupList();
		return {
			...toRefs(state),
			console: window.console,
		};
	},
});
